import { LGreen, Pyro } from "../../../../styledCpns/DataDisplay";
import {
  CatalystPaDesc,
  CaStamina,
  lightPAs,
  TalentLvMults
} from "../../config";
import Yanfei from "./index";
import { checkCharMC } from "../../../../helpers/calculators";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Seal of Approval",
    image: "1/14/Talent_Seal_of_Approval",
    desc: [
      {
        heading: "NA",
        content: (
          <>
            Shoots fireballs that deal up to three counts of{" "}
            <Pyro>Pyro DMG</Pyro>.
            <br />
            When Yanfei's Normal Attacks hit enemies, they will grant her a
            single Scarlet Seal. Yanfei may possess a maximum of 3 Scarlet
            Seals, and each time this effect is triggered, the duration of
            currently possessed Scarlet Seals will refresh.
            <br />
            Each Scarlet Seal will decrease Yanfei's Stamina consumption and
            will disappear when she leaves the field.
          </>
        )
      },
      {
        heading: "CA",
        content: (
          <>
            Consumes Stamina and all Scarlet Seals before dealing AoE{" "}
            <Pyro>Pyro DMG</Pyro> to opponents after a short casting time.
            <br />
            This Charged Attack's AoE and DMG will increase according to the
            amount of Scarlet Seals consumed.
          </>
        )
      },
      {
        heading: "PA",
        content: CatalystPaDesc("Pyro", "Yanfei")
      }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 58.34,
        multType: 2
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 52.13,
        multType: 2
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 76.01,
        multType: 2
      },
      {
        name: "Charged Attack",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 98.23,
        multType: 2
      },
      {
        name: "1-Seal Charged Attack",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 115.56,
        multType: 2
      },
      {
        name: "2-Seal Charged Attack",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 132.9,
        multType: 2
      },
      {
        name: "3-Seal Charged Attack",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 150.23,
        multType: 2
      },
      {
        name: "4-Seal Charged Attack",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 167.57,
        multType: 2
      },
      {
        name: "Extra Hit (A4)",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 0,
        multType: 2,
        conditional: true,
        getTlBnes: ({ char, selfMCs }) => ({
          xtraMult: checkCharMC(Yanfei.buffs[1], char, selfMCs.BCs, 1) ? 80 : 0
        })
      },
      CaStamina(50),
      {
        name: "Scarlet Seal Stamina Consumption Decrease",
        noCalc: true,
        getValue: () => "15% per Seal"
      },
      { name: "Scarlet Seal Duration", noCalc: true, getValue: () => "10s" },
      ...lightPAs("catalyst")
    ]
  },
  {
    type: "Elemental Skill",
    name: "Signed Edict",
    image: "a/a3/Talent_Signed_Edict",
    desc: [
      {
        content: (
          <>
            Summons blistering flames that deal AoE <Pyro>Pyro DMG</Pyro>.
            <br />
            Opponents hit by the flames will grant Yanfei the maximum number of
            Scarlet Seals.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 169.6,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "CD",
        value: "9s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Done Deal",
    image: "9/96/Talent_Done_Deal",
    desc: [
      {
        content: (
          <>
            Triggers a spray of intense flames that rush at nearby opponents,
            dealing AoE <Pyro>Pyro DMG</Pyro>, granting Yanfei the maximum
            number of Scarlet Seals, and applying Brilliance to her.
          </>
        )
      },
      {
        heading: "Brilliance",
        get content() {
          return (
            <>
              Has the following effects:
              <br /> Grants Yanfei a Scarlet Seal at fixed intervals.
              <br /> {this.buff}.
              <br />
              The effects of Brilliance will end if Yanfei leaves the field or
              falls in battle.
            </>
          );
        },
        buff: (
          <>
            Increases the <LGreen>DMG</LGreen> dealt by her{" "}
            <LGreen>Charged Attacks</LGreen>
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 182.4,
        multType: 2
      }
    ],
    otherStats: (lv) => [
      {
        name: "Scarlet Seal Grant Interval",
        value: "1s"
      },
      {
        name: "Charged Attack DMG Bonus",
        value: Math.round(334 * TalentLvMults[5][lv]) / 10 + "%"
      },
      {
        name: "Duration",
        value: "15s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ],
    energyCost: 80
  }
];

export default actvTalents;
