import { Electro } from "../../../../styledCpns/DataDisplay";
import {
  CatalystCaDesc,
  CaStamina,
  lightPAs,
  TalentLvMults
} from "../../config";
import Miko from "./index";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import { checkCharMC } from "../../../../helpers/calculators";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Spiritfox Sin-Eater",
    image: "2/23/Talent_Spiritfox_Sin-Eater",
    desc: [
      {
        heading: "NA",
        content: (
          <>
            Summons forth kitsune spirits, initiating a maximum of 3 attacks
            that deal <Electro>Electro DMG</Electro>.
          </>
        )
      },
      { heading: "CA", content: CatalystCaDesc("Electro") },
      {
        heading: "PA",
        content: (
          <>
            Plunges towards the ground from mid-air, damaging all opponents in
            her path with thunderous might. Deals AoE{" "}
            <Electro>Electro DMG</Electro> upon impact with the ground.
          </>
        )
      }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 39.66,
        multType: 2
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 38.52,
        multType: 2
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Elemental"],
        baseMult: 56.89,
        multType: 2
      },
      {
        name: "Charged Attack",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 142.89,
        multType: 2
      },
      CaStamina(50),
      ...lightPAs("catalyst")
    ]
  },
  {
    type: "Elemental Skill",
    name: "Yakan Evocation: Sesshou Sakura",
    image: "9/93/Talent_Yakan_Evocation_Sesshou_Sakura",
    desc: [
      {
        content: (
          <>
            To Yae, such dull tasks as can be accomplished by driving spirits
            out need not be done personally.
            <br />
            Moves swiftly, leaving a Sesshou Sakura behind.
          </>
        )
      },
      {
        heading: "Sesshou Sakura",
        content: (
          <>
            Has the following properties:
            <br /> Periodically strikes one nearby opponent with lightning,
            dealing <Electro>Electro DMG</Electro>.
            <br /> When there are other Sesshou Sakura nearby, their level will
            increase, boosting the DMG dealt by these lightning strikes.
          </>
        )
      },
      {
        content: (
          <>
            This skill has three charges.
            <br />A maximum of 3 Sesshou Sakura can exist simultaneously. The
            initial level of each Sesshou Sakura is 1, and the initial highest
            level each sakura can reach is 3. If a new Sesshou Sakura is created
            too close to an existing one, the existing one will be destroyed.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Sesshou Sakura DMG Lv.1",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 60.67,
        multType: 2
      },
      {
        name: "DMG Lv. 2",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 75.84,
        multType: 2
      },
      {
        name: "DMG Lv. 3",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 94.8,
        multType: 2
      },
      {
        name: "DMG Lv. 4",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 0,
        lockedMult: 118.5,
        multType: 2,
        getTlBnes: ({ char, selfMCs, party }) => {
          const level =
            char["Elemental Skill"] +
            getXtraTalentLv(actvTalents[1], char, party);
          return {
            xtraMult: checkCharMC(Miko.buffs[1], char, selfMCs.BCs, 1)
              ? 118.5 * TalentLvMults[2][level]
              : 0
          };
        }
      }
    ],
    otherStats: () => [
      {
        name: "Duration",
        value: "14s"
      },
      {
        name: "CD",
        value: "4s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Great Secret Art: Tenko Kenshin",
    image: "9/93/Talent_Great_Secret_Art_Tenko_Kenshin",
    desc: [
      {
        content: (
          <>
            Legends of "kitsunetsuki," or the manifestation of a kitsune's
            might, are common in Inazuma's folktales. One that particularly
            captures the imagination is that of the Sky Kitsune, said to cause
            lightning to fall down upon the foes of the Grand Narukami Shrine.
            Summons a lightning strike, dealing AoE{" "}
            <Electro>Electro DMG</Electro>.
            <br />
            When she uses this skill, Yae Miko will unseal nearby Sesshou
            Sakura, destroying their outer forms and transforming them into
            Tenko Thunderbolts that descend from the skies, dealing AoE{" "}
            <Electro>Electro DMG</Electro>. Each Sesshou Sakura destroyed in
            this way will create one Tenko Thunderbolt.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 260,
        multType: 2
      },
      {
        name: "Tenko Thunderbolt DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 333.82,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "CD",
        value: "22s"
      }
    ],
    energyCost: 90
  }
];

export default actvTalents;
