import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { mapToLevels } from "../../../../helpers/misc";
import { getXtraTalentLv } from "../../../../calculators/helpers";

const Mona = {
  code: 16,
  icon: "a/a0/Character_Mona_Thumb",
  sideIcon: "1/1a/Character_Mona_Side_Icon",
  rarity: 5,
  nation: "Mondstadt",
  vision: "Hydro",
  weapon: "Catalyst",
  stats: mapToLevels([
    {
      "Base HP": 810,
      "Base ATK": 22,
      "Base DEF": 51
    },
    {
      "Base HP": 2102,
      "Base ATK": 58,
      "Base DEF": 132
    },
    {
      "Base HP": 2797,
      "Base ATK": 77,
      "Base DEF": 176
    },
    {
      "Base HP": 4185,
      "Base ATK": 115,
      "Base DEF": 263
    },
    {
      "Base HP": 4678,
      "Base ATK": 129,
      "Base DEF": 294,
      "Energy Recharge": 8
    },
    {
      "Base HP": 5383,
      "Base ATK": 148,
      "Base DEF": 338,
      "Energy Recharge": 8
    },
    {
      "Base HP": 6041,
      "Base ATK": 167,
      "Base DEF": 379,
      "Energy Recharge": 16
    },
    {
      "Base HP": 6752,
      "Base ATK": 186,
      "Base DEF": 424,
      "Energy Recharge": 16
    },
    {
      "Base HP": 7246,
      "Base ATK": 200,
      "Base DEF": 455,
      "Energy Recharge": 16
    },
    {
      "Base HP": 7964,
      "Base ATK": 220,
      "Base DEF": 500,
      "Energy Recharge": 16
    },
    {
      "Base HP": 8458,
      "Base ATK": 233,
      "Base DEF": 531,
      "Energy Recharge": 24
    },
    {
      "Base HP": 9184,
      "Base ATK": 253,
      "Base DEF": 576,
      "Energy Recharge": 24
    },
    {
      "Base HP": 9677,
      "Base ATK": 267,
      "Base DEF": 607,
      "Energy Recharge": 32
    },
    {
      "Base HP": 10409,
      "Base ATK": 287,
      "Base DEF": 653,
      "Energy Recharge": 32
    }
  ]),
  actvTalents,
  pasvTalents: [
    {
      type: "Ascension 1",
      name: "Come 'n' Get Me, Hag!",
      image: `8/8f/Talent_"Come_%27n%27_Get_Me%2C_Hag%21"`,
      desc: (
        <>
          After she has used Illusory Torrent for 2s, if there are any opponents
          nearby, Mona will automatically create a Phantom.
          <br />A Phantom created in this manner lasts for 2s, and its explosion
          DMG is equal to 50% of Mirror Reflection of Doom.
        </>
      )
    },
    {
      type: "Ascension 4",
      name: "Waterborne Destiny",
      image: "6/6a/Talent_Waterborne_Destiny",
      desc: (
        <>
          Increases Mona's <LGreen>Hydro DMG Bonus</LGreen> by a degree
          equivalent to <Green>20%</Green> of her{" "}
          <LGreen>Energy Recharge</LGreen> rate.
        </>
      )
    },
    {
      type: "Passive",
      name: "Principium of Astrology",
      image: "4/48/Talent_Principium_of_Astrology",
      desc: (
        <>
          When Mona crafts <LGreen>Weapon Ascension Materials</LGreen>, she has
          a <Green>25%</Green> <LGreen>chance</LGreen> to refund{" "}
          <Green>one</Green> <LGreen>count</LGreen> of one material out of all
          the crafting materials used.
        </>
      )
    }
  ],
  constellation: [
    {
      name: "Prophecy of Submersion",
      image: "2/27/Constellation_Prophecy_of_Submersion",
      desc: (
        <>
          When any of your own party members hits an opponent affected by an
          Omen, <LGreen>Electro-Charged DMG</LGreen>,{" "}
          <LGreen>Vaporize DMG</LGreen>, <LGreen>Hydro Swirl DMG</LGreen>, and{" "}
          <LGreen>Frozen duration</LGreen> are increased by <Green>15%</Green>{" "}
          for 8s.
        </>
      )
    },
    {
      name: "Lunar Chain",
      image: "1/16/Constellation_Lunar_Chain",
      desc: (
        <>
          When a Normal Attack hits, there is a <Green>20%</Green>{" "}
          <LGreen>chance</LGreen> that it will be automatically followed by a
          Charged Attack.
          <br />
          This effect can only occur once every 5s.
        </>
      )
    },
    {
      name: "Restless Revolution",
      image: "2/2a/Constellation_Restless_Revolution",
      desc: "Stellaris Phantasm"
    },
    {
      name: "Prophecy of Oblivion",
      image: "3/38/Constellation_Prophecy_of_Oblivion",
      desc: (
        <>
          When any party member attacks an opponent affected by an Omen, their{" "}
          <LGreen>CRIT Rate</LGreen> is increased by <Green>15%</Green>.
        </>
      )
    },
    {
      name: "Mockery of Fortuna",
      image: "b/bd/Constellation_Mockery_of_Fortuna",
      desc: "Mirror Reflection of Doom"
    },
    {
      name: "Rhetorics of Calamitas",
      image: "6/62/Constellation_Rhetorics_of_Calamitas",
      desc: (
        <>
          Upon entering Illusory Torrent, Mona gains a <Green>60%</Green>{" "}
          <LGreen>DMG increase</LGreen> of her next{" "}
          <LGreen>Charged Attack</LGreen> per second of movement (up to{" "}
          <Green>180%</Green>) for 8s.
        </>
      )
    }
  ],
  buffs: [
    {
      index: 0,
      src: "Elemental Burst",
      desc: ({ toSelf, char, party, inputs }) => (
        <>
          Omen increases{" "}
          <Green>{Mona.buffs[0].bnValue(toSelf, char, party, inputs)}%</Green>{" "}
          <LGreen>DMG taken</LGreen> by opponents. (This is actually a DMG
          bonus.)
        </>
      ),
      isGranted: () => true,
      affect: "party",
      labels: ["Elemental Burst Level"],
      inputs: [1],
      inputTypes: ["text"],
      maxs: [13],
      addBnes: ({ bonuses, toSelf, char, party, inputs }) => {
        bonuses.AllDmg += Mona.buffs[0].bnValue(toSelf, char, party, inputs);
      },
      bnValue: (toSelf, char, party, inputs) => {
        const level = toSelf
          ? char["Elemental Burst"] +
            getXtraTalentLv(Mona.actvTalents[2], char, party)
          : inputs[0];
        return Math.min(40 + level * 2, 60);
      }
    },
    {
      index: 1,
      src: "Ascension 4 Passive Talent",
      desc: ({ ATTRs }) => (
        <>
          {Mona.pasvTalents[1].desc}{" "}
          <Red>Hydro DMG Bonus: {Mona.buffs[1].bnValue(ATTRs)}%.</Red>
        </>
      ),
      isGranted: (char) => char.ascension >= 4,
      affect: "self",
      addBnes: ({ ATTRs }) => {
        ATTRs["Hydro DMG Bonus"] += Mona.buffs[1].bnValue(ATTRs);
      },
      bnValue: (ATTRs) => Math.round(ATTRs["Energy Recharge"] * 2) / 10
    },
    {
      index: 2,
      src: "Constellation 1",
      desc: () => Mona.constellation[0].desc,
      isGranted: (char) => char.constellation >= 1,
      affect: "party",
      addBnes: ({ rxnBnes }) => {
        rxnBnes["Electro-Charged"] += 15;
        rxnBnes.Swirl += 15;
        rxnBnes.Vaporize += 15;
      }
    },
    {
      index: 3,
      src: "Constellation 4",
      desc: () => Mona.constellation[3].desc,
      isGranted: (char) => char.constellation >= 4,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["CRIT Rate"] += 15;
      }
    },
    {
      index: 4,
      src: "Constellation 6",
      desc: () => Mona.constellation[5].desc,
      isGranted: (char) => char.constellation >= 6,
      affect: "self",
      selfLabels: ["Stacks"],
      inputs: [1],
      inputTypes: ["select"],
      maxs: [3],
      addBnes: ({ bonuses, inputs }) => {
        bonuses.CA.DMG += 60 * inputs[0];
      }
    }
  ]
};

export default Mona;
