import { Green, LGreen, Red } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { TravelerInfo } from "../../config";
import { checkCharMC } from "../../../../helpers/calculators";

const ElectroMC = {
  ...TravelerInfo,
  code: 46,
  vision: "Electro",
  actvTalents,
  pasvTalents: [
    {
      type: "Ascension 1",
      name: "Thunderflash",
      image: "1/16/Talent_Thunderflash",
      desc: (
        <>
          When another nearby character in the party obtains an Abundance Amulet
          created by Lightning Blade, Lightning Blade's <LGreen>CD</LGreen> is
          decreased by <Green>1.5s</Green>.
        </>
      )
    },
    {
      type: "Ascension 4",
      name: "Resounding Roar",
      image: "2/26/Talent_Resounding_Roar",
      desc: (
        <>
          Increases the <LGreen>Energy Recharge</LGreen> effect granted by
          Lightning Blade's Abundance Amulet by <Green>10%</Green> of the
          Traveler's <LGreen>Energy Recharge</LGreen>.
        </>
      )
    }
  ],
  constellation: [
    {
      name: "Spring Thunder of Fertility",
      image: "2/2f/Constellation_Spring_Thunder_of_Fertility",
      desc: (
        <>
          The number of <LGreen>Abundance Amulets</LGreen> that can be generated
          using Lightning Blade is increased to <Green>3</Green>.
        </>
      )
    },
    {
      name: "Violet Vehemence",
      image: "8/8f/Constellation_Violet_Vehemence",
      desc: (
        <>
          When Falling Thunder created by Bellowing Thunder hits an opponent, it
          will decrease their <LGreen>Electro RES</LGreen> by <Green>15%</Green>{" "}
          for 8s.
        </>
      )
    },
    {
      name: "Distant Crackling",
      image: "c/c0/Constellation_Distant_Crackling",
      desc: "Bellowing Thunder"
    },
    {
      name: "Fickle Cloudstrike",
      image: "8/84/Constellation_Fickle_Cloudstrike",
      desc: (
        <>
          When a character obtains Abundance Amulets generated by Lightning
          Blade, if this character's Energy is less than 35%, the{" "}
          <LGreen>Energy</LGreen> restored by the{" "}
          <LGreen>Abundance Amulets</LGreen> is increased by <Green>100%</Green>
          .
        </>
      )
    },
    {
      name: "Clamor in the Wilds",
      image: "8/80/Constellation_Clamor_in_the_Wilds",
      desc: "Lightning Blade"
    },
    {
      name: "World-Shaker",
      image: "7/76/Constellation_World-Shaker",
      desc: (
        <>
          Every 2 Falling Thunder attacks triggered by Bellowing Thunder will
          significantly increase the DMG dealt by the next Falling Thunder,
          dealing <Green>200%</Green> of its <LGreen>original DMG</LGreen>, and
          will restore an <LGreen>additional</LGreen> <Green>1</Green>{" "}
          <LGreen>Energy</LGreen> to the current character.
        </>
      )
    }
  ],
  buffs: [
    {
      index: 0,
      src: "Elemental Skill",
      desc: ({ toSelf, char, inputs, ATTRs, charBCs }) => {
        const { bnValue } = ElectroMC.buffs[0];
        let result;
        if (toSelf) {
          const A4on = checkCharMC(ElectroMC.buffs[1], char, charBCs, 1);
          result = bnValue(A4on, ATTRs["Energy Recharge"]);
          if (charBCs[0].activated && A4on) {
            result = Math.round(result * 9.1) / 10;
          }
        } else {
          result = bnValue(...inputs);
        }
        return (
          <>
            {ElectroMC.actvTalents[1].desc[1].buff}{" "}
            <Red>Total bonus: {result}%</Red>.
          </>
        );
      },
      isGranted: () => true,
      affect: "party",
      labels: ["A4 Passive Talent", "Energy Recharge"],
      inputs: [false, 100],
      inputTypes: ["check", "text"],
      maxs: [null, 999],
      addBnes: ({ ATTRs, char, inputs, toSelf, charBCs }) => {
        const { bnValue } = ElectroMC.buffs[0];
        ATTRs["Energy Recharge"] += toSelf
          ? bnValue(
              checkCharMC(ElectroMC.buffs[1], char, charBCs, 1),
              ATTRs["Energy Recharge"]
            )
          : bnValue(...inputs);
      },
      bnValue: (boosted, ER) => 20 + (boosted ? Math.round(ER) / 10 : 0)
    },
    {
      index: 1,
      src: "Ascension 4 Passive Talent",
      desc: () => ElectroMC.pasvTalents[1].desc,
      isGranted: (char) => char.ascension >= 4,
      affect: "self"
    }
  ],
  debuffs: [
    {
      index: 0,
      src: "Constellation 2",
      desc: () => ElectroMC.constellation[1].desc,
      isGranted: (char) => char.constellation >= 2,
      addPntes: ({ rdMult }) => {
        rdMult["Electro RES"] -= 15;
      }
    }
  ]
};

export default ElectroMC;
