import { LGreen, Green, Red, Pyro } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import actvTalents from "./talents";
import { mapToLevels } from "../../../../helpers/misc";
import { checkCharMC } from "../../../../helpers/calculators";
import { getXtraTalentLv } from "../../../../calculators/helpers";

const Bennett = {
  code: 19,
  icon: "7/7b/Character_Bennett_Thumb",
  sideIcon: "3/3c/Character_Bennett_Side_Icon",
  rarity: 4,
  nation: "Mondstadt",
  vision: "Pyro",
  weapon: "Sword",
  stats: mapToLevels([
    {
      "Base HP": 1039,
      "Base ATK": 16,
      "Base DEF": 65
    },
    {
      "Base HP": 2670,
      "Base ATK": 41,
      "Base DEF": 166
    },
    {
      "Base HP": 3447,
      "Base ATK": 53,
      "Base DEF": 214
    },
    {
      "Base HP": 5163,
      "Base ATK": 80,
      "Base DEF": 321
    },
    {
      "Base HP": 5715,
      "Base ATK": 88,
      "Base DEF": 356,
      "Energy Recharge": 6.7
    },
    {
      "Base HP": 6573,
      "Base ATK": 101,
      "Base DEF": 409,
      "Energy Recharge": 6.7
    },
    {
      "Base HP": 7309,
      "Base ATK": 113,
      "Base DEF": 455,
      "Energy Recharge": 13.3
    },
    {
      "Base HP": 8168,
      "Base ATK": 126,
      "Base DEF": 508,
      "Energy Recharge": 13.3
    },
    {
      "Base HP": 8719,
      "Base ATK": 134,
      "Base DEF": 542,
      "Energy Recharge": 13.3
    },
    {
      "Base HP": 9577,
      "Base ATK": 148,
      "Base DEF": 596,
      "Energy Recharge": 13.3
    },
    {
      "Base HP": 10129,
      "Base ATK": 156,
      "Base DEF": 630,
      "Energy Recharge": 20
    },
    {
      "Base HP": 10987,
      "Base ATK": 169,
      "Base DEF": 684,
      "Energy Recharge": 20
    },
    {
      "Base HP": 11539,
      "Base ATK": 178,
      "Base DEF": 718,
      "Energy Recharge": 26.7
    },
    {
      "Base HP": 12397,
      "Base ATK": 191,
      "Base DEF": 771,
      "Energy Recharge": 26.7
    }
  ]),
  actvTalents,
  pasvTalents: [
    {
      type: "Ascension 1",
      name: "Rekindle",
      image: "7/77/Talent_Rekindle",
      desc: (
        <>
          Decreases Passion Overload's <LGreen>CD</LGreen> by <Green>20%</Green>
          .
        </>
      )
    },
    {
      type: "Ascension 4",
      name: "Fearnaught",
      image: "1/1a/Talent_Fearnaught",
      desc: (
        <>
          Within the area created by Fantastic Voyage, Passion Overload takes on
          the following effects:
          <br /> <LGreen>CD</LGreen> is reduced by <Green>50%</Green>.
          <br /> Bennett will not be launched by the effects of Charge Level 2.
        </>
      )
    },
    {
      type: "Passive",
      name: "It Should Be Safe...",
      image: "2/2a/Talent_It_Should_Be_Safe...",
      desc: (
        <>
          When dispatched on an <LGreen>expedition</LGreen> in Mondstadt,{" "}
          <LGreen>time consumed</LGreen> is reduced by <Green>25%</Green>.
        </>
      )
    }
  ],
  constellation: [
    {
      name: "Grand Expectation",
      image: "c/c0/Constellation_Grand_Expectation",
      get desc() {
        return (
          <>
            Fantastic Voyage's ATK increase no longer has an HP restriction, and
            {this.buff}
          </>
        );
      },
      buff: (
        <>
          gains an additional <Green>20%</Green> of Bennett's{" "}
          <LGreen>Base ATK</LGreen>.
        </>
      )
    },
    {
      name: "Impasse Conqueror",
      image: "8/87/Constellation_Impasse_Conqueror",
      desc: (
        <>
          When Bennett's HP falls below 70%, his{" "}
          <LGreen>Energy Recharge</LGreen> is increased by <Green>30%</Green>.
        </>
      )
    },
    {
      name: "Unstoppable Fervor",
      image: "e/ed/Constellation_Unstoppable_Fervor",
      desc: "Passion Overload"
    },
    {
      name: "Unexpected Odyssey",
      image: "0/0e/Constellation_Unexpected_Odyssey",
      desc: (
        <>
          Using a Normal Attack when executing the second attack of Passion
          Overload's Charge Level 1 allows an additional attack to be performed.
          <br />
          This additional attack does <Green>135%</Green> of the{" "}
          <LGreen>second attack's DMG</LGreen>.
        </>
      )
    },
    {
      name: "True Explorer",
      image: "3/39/Constellation_True_Explorer",
      desc: "Fantastic Voyage"
    },
    {
      name: "Fire Ventures with Me",
      image: "6/6c/Constellation_Fire_Ventures_with_Me",
      desc: (
        <>
          Sword, Claymore, Polearm characters inside Fantastic Voyage's radius
          gain a <Green>15%</Green> <LGreen>Pyro DMG Bonus</LGreen> and their
          weapons are <LGreen>infused</LGreen> with <Pyro>Pyro</Pyro>.
        </>
      )
    }
  ],
  buffs: [
    {
      index: 0,
      src: "Elemental Burst",
      desc: ({ toSelf, inputs }) => (
        <>
          The character within its AoE gains an <LGreen>ATK Bonus</LGreen> that
          is based on Bennett's <LGreen>Base ATK</LGreen>.{" "}
          {!toSelf && (
            <Red>ATK Bonus: {Bennett.buffs[0].bnValue(...inputs)}.</Red>
          )}
        </>
      ),
      isGranted: () => true,
      affect: "party",
      labels: ["Base ATK", "Elemental Burst Level", "Constellation 1"],
      inputs: [0, 1, false],
      inputTypes: ["text", "text", "check"],
      maxs: [9999, 13, null],
      addBnes: ({ ATTRs, inputs, toSelf, char, charBCs, party }) => {
        const { bnValue } = Bennett.buffs[0];
        ATTRs.ATK += toSelf
          ? bnValue(
              ATTRs["Base ATK"],
              char["Elemental Burst"] +
                getXtraTalentLv(Bennett.actvTalents[2], char, party),
              checkCharMC(Bennett.buffs[1], char, charBCs, 1)
            )
          : bnValue(...inputs);
      },
      bnValue: (baseATK, level, boosted) =>
        Math.round(
          baseATK * ((56 * TalentLvMults[2][level] + (boosted ? 20 : 0)) / 100)
        )
    },
    {
      index: 1,
      src: "Constellation 1",
      desc: () => (
        <>Fantastic Voyage's ATK increase {Bennett.constellation[0].buff}</>
      ),
      isGranted: (char) => char.constellation >= 1,
      affect: "self"
    },
    {
      index: 2,
      src: "Constellation 6",
      desc: () => Bennett.constellation[5].desc,
      isGranted: (char) => char.constellation >= 6,
      affect: "party",
      addBnes: ({ ATTRs }) => {
        ATTRs["Pyro DMG Bonus"] += 15;
      },
      canInfuse: (charData) =>
        ["Sword", "Claymore", "Polearm"].includes(charData.weapon),
      infuseElmt: "Pyro",
      infuseRange: ["NA", "CA", "PA"],
      canBeOverrided: true
    }
  ]
};

export default Bennett;
