import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import actvTalents from "./talents";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import { mapToLevels } from "../../../../helpers/misc";
import { checkCharMC, findInput } from "../../../../helpers/calculators";

export const Aloy = {
  code: 39,
  name: "Aloy",
  icon: "6/6a/Character_Aloy_Thumb",
  sideIcon: "0/0c/Character_Aloy_Side_Icon",
  rarity: 5,
  nation: "Outland",
  vision: "Cryo",
  weapon: "Bow",
  stats: mapToLevels([
    {
      "Base HP": 848,
      "Base ATK": 18,
      "Base DEF": 53
    },
    {
      "Base HP": 2201,
      "Base ATK": 47,
      "Base DEF": 137
    },
    {
      "Base HP": 2928,
      "Base ATK": 63,
      "Base DEF": 182
    },
    {
      "Base HP": 4382,
      "Base ATK": 94,
      "Base DEF": 272
    },
    {
      "Base HP": 4899,
      "Base ATK": 105,
      "Base DEF": 304,
      "Cryo DMG Bonus": 7.2
    },
    {
      "Base HP": 5636,
      "Base ATK": 121,
      "Base DEF": 350,
      "Cryo DMG Bonus": 7.2
    },
    {
      "Base HP": 6325,
      "Base ATK": 136,
      "Base DEF": 393,
      "Cryo DMG Bonus": 14.4
    },
    {
      "Base HP": 7070,
      "Base ATK": 152,
      "Base DEF": 439,
      "Cryo DMG Bonus": 14.4
    },
    {
      "Base HP": 7587,
      "Base ATK": 163,
      "Base DEF": 471,
      "Cryo DMG Bonus": 14.4
    },
    {
      "Base HP": 8339,
      "Base ATK": 179,
      "Base DEF": 517,
      "Cryo DMG Bonus": 14.4
    },
    {
      "Base HP": 8856,
      "Base ATK": 190,
      "Base DEF": 550,
      "Cryo DMG Bonus": 21.6
    },
    {
      "Base HP": 9616,
      "Base ATK": 206,
      "Base DEF": 597,
      "Cryo DMG Bonus": 21.6
    },
    {
      "Base HP": 10133,
      "Base ATK": 217,
      "Base DEF": 629,
      "Cryo DMG Bonus": 28.8
    },
    {
      "Base HP": 10899,
      "Base ATK": 234,
      "Base DEF": 676,
      "Cryo DMG Bonus": 28.8
    }
  ]),
  actvTalents,
  pasvTalents: [
    {
      type: "Ascension 1",
      name: "Combat Override",
      image: "0/01/Talent_Combat_Override",
      desc: (
        <>
          When Aloy receives the Coil effect from Frozen Wilds, her{" "}
          <LGreen>ATK</LGreen> is increased by <Green>16%</Green>, while nearby
          party members' <LGreen>ATK</LGreen> is increased by <Green>8%</Green>.
          This effect lasts 10s.
        </>
      )
    },
    {
      type: "Ascension 4",
      name: "Strong Strike",
      image: "b/ba/Talent_Strong_Strike",
      desc: (
        <>
          When Aloy is in the Rushing Ice state conferred by Frozen Wilds, her{" "}
          <LGreen>Cryo DMG Bonus</LGreen> increases by <Green>3.5%</Green> every
          1s, up to <Green>35%</Green>.
        </>
      )
    },
    {
      type: "Passive",
      name: "Easy Does It",
      image: "0/0f/Talent_Easy_Does_It",
      desc: (
        <>
          When Aloy is in the party, animals who produce Fowl, Raw Meat, or
          Chilled Meat will not be startled when party members approach them.
        </>
      )
    }
  ],
  constellation: [],
  buffs: [
    {
      index: 0,
      src: "Coil stacks",
      desc: ({ char, charBCs, party }) => (
        <>
          {Aloy.actvTalents[1].desc[1].lines[0]}{" "}
          <Red>
            Total DMG Bonus: {Aloy.buffs[0].bnValue(char, charBCs, party)}%.
          </Red>
        </>
      ),
      isGranted: () => true,
      affect: "self",
      selfLabels: ["Stacks"],
      inputs: [1],
      inputTypes: ["select"],
      maxs: [3],
      addBnes: ({ bonuses, char, charBCs, party }) => {
        bonuses.NA.DMG += Aloy.buffs[0].bnValue(char, charBCs, party);
      },
      bnValue: (char, charBCs, party) => {
        const level =
          char["Elemental Skill"] +
          getXtraTalentLv(Aloy.actvTalents[1], char, party);
        const stacks = checkCharMC(null, char, charBCs, 1, true)
          ? 5
          : findInput(charBCs, 0, 0);
        return Math.round(584.6 * TalentLvMults[5][level] * stacks) / 100;
      }
    },
    {
      index: 1,
      src: "Max Coil stacks",
      desc: () => Aloy.actvTalents[1].desc[1].lines[1],
      isGranted: () => true,
      affect: "self",
      canInfuse: () => true,
      infuseRange: ["NA"],
      canBeOverrided: false
    },
    {
      index: 2,
      src: "Ascension 1 Passive Talent",
      desc: () => Aloy.pasvTalents[0].desc,
      isGranted: (char) => char.ascension >= 1,
      affect: "party",
      addBnes: ({ ATTRs, toSelf }) => {
        ATTRs["ATK%"] += toSelf ? 16 : 8;
      }
    },
    {
      index: 3,
      src: "Ascension 4 Passive Talent",
      desc: () => Aloy.pasvTalents[1].desc,
      isGranted: (char) => char.ascension >= 4,
      affect: "self",
      selfLabels: ["Stacks"],
      inputs: [1],
      inputTypes: ["select"],
      maxs: [10],
      addBnes: ({ ATTRs, inputs }) => {
        ATTRs["Cryo DMG Bonus"] += 3.5 * inputs[0];
      }
    }
  ]
};

export default Aloy;
